<template>
	<div class="nav-btn" @click="onToggle">
		<div class="btn-bar menu"></div>
		<div class="btn-bar menu"></div>
		<div class="btn-bar menu"></div>
		<div class="btn-bar close"></div>
		<div class="btn-bar close"></div>
	</div>
</template>
<script>
export default {
	methods: {
		onToggle () {
			this.$emit('toggle')
		}
	}
}
</script>
<style lang="scss" scoped>
$btn-bar-width:  32px;
$btn-bar-height:  2px;
$btn-bar-height-close:  2px;
$btn-bar-spacing: 3px;
$btn-bar-bcg: $light_pink;
$menu-bar-anim-time:   0.3s;
$menu-bar-anim-offset: 400%;

$close-bar-anim-time:   0.5s;
$close-bar-anim-offset: 250%;

$background-anim-time: $close-bar-anim-time;

.nav-btn {
	position: relative;
	z-index: 998;
	cursor: pointer;
	width: $btn-bar-width;
	height: 20px;
	display: block;
	.btn-bar {
		background: $btn-bar-bcg;
		border-radius: 0;
		position: relative;
		width: $btn-bar-width;
		&.menu {
			transition:
			transform $menu-bar-anim-time ease-in-out $close-bar-anim-time,
			opacity ($menu-bar-anim-time / 2) ease-in-out $close-bar-anim-time;
			height: $btn-bar-height;
			&:not(:first-child) {
				margin-top: $btn-bar-spacing;
			}
			&:nth-child(1) {
				transform-origin: top right;
				transform: rotate(0deg);
				width: $btn-bar-width;
			}
			&:nth-child(2) {
				transform: rotate(0deg) translate(0px, 3px);
				width: $btn-bar-width;
				transition: .3s;
			}
			&:nth-child(3) {
				transform: rotate(0deg) translate(0px, 6px);
				width: calc(16px);
				transition: .3s;
				position: absolute;
			}
		}
		&.close {
			transform-origin: center center;
			position: absolute;
			margin-top: -$btn-bar-height / 2;
			opacity: 0;
			top: 50%;
			transition:
			transform $close-bar-anim-time ease-in-out,
			opacity ($close-bar-anim-time / 4) ease-in-out ($close-bar-anim-time / 2);
			height: $btn-bar-height-close;
		}
		&:not(:last-child) {
			transform: rotate( 45deg) translateX(-$close-bar-anim-offset);
		}
		&:last-child {
			transform: rotate(-45deg) translateX( $close-bar-anim-offset);
		}
	}
	&:hover {
		.btn-bar.menu:nth-child(3) {
			width: $btn-bar-width;
		}
	}
}
.open {
	.btn-bar {
		&.menu  {
			opacity: 0;
			transition:
			transform $menu-bar-anim-time ease-in-out,
			opacity ($menu-bar-anim-time / 2) ease-in-out ($menu-bar-anim-time / 2);
		}
		&.close {
			opacity: 1;
			transition:
			transform $close-bar-anim-time ease-in-out $menu-bar-anim-time,
			opacity ($close-bar-anim-time / 2) ease-in-out $menu-bar-anim-time;
			&:not(:last-child) {
				transform: rotate( 45deg);
			}
			&:last-child {
				transform: rotate(-45deg);
			}
		}
		&:nth-child(1) {
			transform: rotate(-45deg) translateX(-$menu-bar-anim-offset);
		}
		&:nth-child(2) {
			transform: rotate(-45deg) translate(0, -4px) translateX( $menu-bar-anim-offset / 2);
		}
		&:nth-child(3) {
			transform: rotate(-45deg) translate(0, 1px) translateX( $menu-bar-anim-offset / 2);
		}
	}
}
</style>
