import Vue from 'vue'
import Vuex from 'vuex'
import moduleHome from '@/store/modules/home.js'

Vue.use(Vuex)

export default new Vuex.Store({
	state: {
		isSpinner: false
	},
	mutations: {
		SET_IS_SPINNER (state, payload) {
      state.isSpinner = payload
    }
	},
	actions: {},
	modules: {
		home: moduleHome
	}
})
