<template>
  <div
    v-if="isSpinner"
    id="spinner"
    class="container-spinner"
  >
    <looping-rhombuses-spinner
      :animation-duration="2500"
      :rhombus-size="15"
      :color="'#D775FF'"
    />
  </div>
</template>
<script>
import { LoopingRhombusesSpinner } from 'epic-spinners'
export default {
  components: {
    LoopingRhombusesSpinner
  },
  computed: {
    isSpinner () {
      const isS = this.$store.state.isSpinner
      document.querySelector('body').style.overflow = isS ? 'hidden' : 'auto'
      return isS
    }
  }
}
</script>
<style scoped>
  .container-spinner {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: rgba(0,0,0,.9);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1500;
  }
</style>
