import { itemsHomeMenu } from '@/data'
console.log(itemsHomeMenu)
export default {
	data () {
		return {
			isOpen: false,
			items: itemsHomeMenu
		}
	},
	components: {
		Hamburger1,
		Menu1,
		ItemMenu
	},
	created () {
		console.log(this.items)
	},
	methods: {
