<template>
	<div class="container-canvas" id="container-canvas"></div>
</template>

<script>
import THREELib from 'three-js'
const THREE = THREELib()
export default {
	data () {
		return {
			container: null,
			containerSize: null,
			camera: null,
			scene: null,
			renderer: null,
			group: null,
			mouseX: 0,
			mouseY: 0,
			windowHalfX: null,
			windowHalfY: null
		}
	},
	mounted () {
		this.init()
		this.animate()
	},
	components: {
	},
	methods: {
		init () {
			this.container = document.getElementById('container-canvas')
			this.containerSize = document.getElementById('container-main-screen')
			this.windowHalfX = window.innerWidth / 2
			this.windowHalfY = window.innerHeight / 2

			this.camera = new THREE.PerspectiveCamera(60, window.innerWidth / window.innerHeight, 1, 100000)
			this.camera.position.z = 800
			this.scene = new THREE.Scene()
			const geometry = new THREE.BoxBufferGeometry(50, 10, 0)
			const geometry1 = new THREE.BoxBufferGeometry(50, 15, 0)
			const geometry2 = new THREE.BoxBufferGeometry(25, 10, 0)
			const material = new THREE.MeshBasicMaterial({ opacity: 0.2 })
			const material1 = new THREE.MeshBasicMaterial({ opacity: 0.2 })
			const material2 = new THREE.MeshBasicMaterial({ opacity: 0.2 })

			this.group = new THREE.Group()

			for (let i = 0; i < 70; i++) {
				const mesh = new THREE.Mesh(geometry, material)
				mesh.material.color.set('#381449')
				mesh.position.x = Math.random() * 2000 - 1000
				mesh.position.y = Math.random() * 800 - 400
				mesh.position.z = Math.random() * 300 - 100
				mesh.rotation.y = Math.random() * 4 * Math.PI
				mesh.matrixAutoUpdate = false
				mesh.updateMatrix()
				this.group.add(mesh)
			}

			for (let i = 0; i < 70; i++) {
				const mesh = new THREE.Mesh(geometry1, material1)
				mesh.material.color.set('#D775FF')
				mesh.position.x = Math.random() * 2000 - 1000
				mesh.position.y = Math.random() * 800 - 400
				mesh.position.z = Math.random() * 300 - 100
				mesh.rotation.y = Math.random() * 4 * Math.PI
				mesh.matrixAutoUpdate = false
				mesh.updateMatrix()
				this.group.add(mesh)
			}

			for (let i = 0; i < 30; i++) {
				const mesh = new THREE.Mesh(geometry2, material2)
				mesh.material.color.set('#520F6E')
				mesh.position.x = Math.random() * 2000 - 1000
				mesh.position.y = Math.random() * 800 - 400
				mesh.position.z = Math.random() * 300 - 100
				mesh.rotation.y = Math.random() * 4 * Math.PI
				mesh.matrixAutoUpdate = false
				mesh.updateMatrix()
				this.group.add(mesh)
			}

			this.scene.add(this.group)

			this.renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true })
			this.renderer.setClearColor(0x000000, 0)
			this.renderer.setPixelRatio(window.devicePixelRatio)
			this.renderer.setSize(this.containerSize.offsetWidth, this.containerSize.offsetHeight)
			this.container.appendChild(this.renderer.domElement)
			document.addEventListener('mousemove', this.onDocumentMouseMove, false)
			window.addEventListener('resize', this.onWindowResize, false)
		},
		onWindowResize () {
			this.windowHalfX = window.innerWidth / 2
			this.windowHalfY = window.innerHeight / 2
			this.camera.aspect = window.innerWidth / window.innerHeight
			this.camera.updateProjectionMatrix()
			this.renderer.setSize(window.innerWidth, window.innerHeight)
		},
		onDocumentMouseMove (event) {
			this.mouseX = (event.clientX - this.windowHalfX) * 10
			this.mouseY = (event.clientY - this.windowHalfY) * 10
		},
		animate () {
			requestAnimationFrame(this.animate)
			this.renderThree()
		},
		renderThree () {
			// const time = Date.now() * 0.0001
			// const rx = Math.sin( time * 0.7 ) * 0.5
			// const ry = Math.sin(time * 0.3) * 0.5
			// const rz = Math.sin(time * 0.1) * 0.5
			this.camera.position.x = (this.mouseX - this.camera.position.x) * 0.02
			this.camera.position.y = (-this.mouseY - this.camera.position.y) * 0.02
			this.camera.lookAt(this.scene.position)
			this.renderer.render(this.scene, this.camera)
		}
	}
}
</script>
<style scoped lang="scss">
	.container-canvas {
		position: absolute;
		top: 0px;
		left: 0;
	}
</style>
